<template>
  <view class="payment-container">
    <!-- È°∂ÈÉ®ÂØºËà™Ê†è -->
    <view class="nav-bar">
      <view class="nav-back" @click="goBack">
        <text class="iconfont">‚Äπ</text>
      </view>
      <view class="nav-title">Á°ÆËÆ§ÊîØ‰ªò</view>
      <view class="nav-placeholder"></view>
    </view>

    <!-- ÊîØ‰ªòÂÜÖÂÆπ -->
    <view class="payment-content">
      <!-- ËÆ¢ÂçïÈáëÈ¢ù -->
      <view class="amount-card">
        <view class="amount-label">ÊîØ‰ªòÈáëÈ¢ù</view>
        <view class="amount-value">
          <text class="amount-symbol">¬•</text>
          <text class="amount-number">{{ totalAmount }}</text>
        </view>
      </view>

      <!-- ËÆ¢Âçï‰ø°ÊÅØ -->
      <view class="order-info-section" v-if="orderInfo">
        <view class="section-title">ËÆ¢Âçï‰ø°ÊÅØ</view>
        <view class="info-list">
          <view class="info-item">
            <text class="info-label">ËÆ¢ÂçïÁºñÂè∑</text>
            <text class="info-value">{{ orderInfo.orderNo || '-' }}</text>
          </view>
          <view class="info-item">
            <text class="info-label">ÊúçÂä°Á±ªÂûã</text>
            <text class="info-value">{{ getServiceTypeName(orderInfo.serviceType) }}</text>
          </view>
          <view class="info-item">
            <text class="info-label">Áâ©ÂìÅ‰ø°ÊÅØ</text>
            <text class="info-value">{{ orderInfo.goodsInfo || '-' }}</text>
          </view>
        </view>
      </view>

      <!-- ÊîØ‰ªòÊñπÂºè -->
      <view class="payment-method-section">
        <view class="section-title">ÊîØ‰ªòÊñπÂºè</view>
        <view class="payment-method-list">
          <!-- ‰ΩôÈ¢ùÊîØ‰ªò -->
          <view class="payment-method-item" @click="selectPayType(1)">
            <view class="method-left">
              <view class="method-icon">
                <text>üí∞</text>
              </view>
              <view class="method-info">
                <text class="method-name">‰ΩôÈ¢ùÊîØ‰ªò</text>
                <text class="method-desc">ÂΩìÂâç‰ΩôÈ¢ùÔºö¬•{{ balance }}</text>
              </view>
            </view>
            <view class="method-check" :class="{ active: payType === 1 }">
              <text class="check-icon" v-if="payType === 1">‚úì</text>
            </view>
          </view>

          <!-- ÂæÆ‰ø°ÊîØ‰ªòÔºàÊú™ÂºÄÈÄöÔºâ -->
          <view class="payment-method-item disabled">
            <view class="method-left">
              <view class="method-icon">
                <text>üíö</text>
              </view>
              <view class="method-info">
                <text class="method-name">ÂæÆ‰ø°ÊîØ‰ªò</text>
                <text class="method-desc">ÊöÇÊú™ÂºÄÈÄö</text>
              </view>
            </view>
            <view class="method-tag">Âç≥Â∞ÜÂºÄÊîæ</view>
          </view>
        </view>
      </view>

      <!-- ÊîØ‰ªòËØ¥Êòé -->
      <view class="tips-section">
        <view class="tips-title">ÊîØ‰ªòËØ¥Êòé</view>
        <view class="tips-list">
          <text class="tip-item">‚Ä¢ ËÆ¢ÂçïÊèê‰∫§Âêé30ÂàÜÈíüÂÜÖÊú™ÊîØ‰ªòÂ∞ÜËá™Âä®ÂèñÊ∂à</text>
          <text class="tip-item">‚Ä¢ ÊîØ‰ªòÊàêÂäüÂêéÔºåËÆ¢ÂçïÂ∞ÜÂèëÈÄÅÁªôÈ™ëÊâã</text>
          <text class="tip-item">‚Ä¢ Â¶ÇÊúâÁñëÈóÆËØ∑ËÅîÁ≥ªÂÆ¢Êúç</text>
        </view>
      </view>
    </view>

    <!-- Â∫ïÈÉ®ÊîØ‰ªòÊåâÈíÆ -->
    <view class="bottom-bar">
      <view class="bar-info">
        <text class="bar-label">ÊîØ‰ªòÈáëÈ¢ùÔºö</text>
        <text class="bar-amount">¬•{{ totalAmount }}</text>
      </view>
      <button class="btn-pay" @click="confirmPay" :disabled="paying">
        {{ paying ? 'ÊîØ‰ªò‰∏≠...' : 'Á°ÆËÆ§ÊîØ‰ªò' }}
      </button>
    </view>
  </view>
</template>

<script>
import { payOrder, getOrderDetail } from '@/api/order.js'
import { getWalletBalance } from '@/api/wallet.js'

export default {
  data() {
    return {
      orderId: null,
      totalAmount: '0.00',
      orderInfo: null,
      payType: 1, // 1-‰ΩôÈ¢ùÊîØ‰ªò
      balance: 0, // ‰ΩôÈ¢ù
      paying: false // ÊîØ‰ªò‰∏≠
    }
  },

  onLoad(options) {
    if (options.orderId) {
      this.orderId = options.orderId
    }
    if (options.totalAmount) {
      this.totalAmount = Number(options.totalAmount).toFixed(2)
    }

    // Âä†ËΩΩËÆ¢ÂçïËØ¶ÊÉÖ
    if (this.orderId) {
      this.loadOrderDetail()
    }

    // Âä†ËΩΩ‰ΩôÈ¢ù
    this.loadBalance()
  },

  methods: {
    /**
     * Âä†ËΩΩËÆ¢ÂçïËØ¶ÊÉÖ
     */
    async loadOrderDetail() {
      try {
        const res = await getOrderDetail(this.orderId)
        if (res.code === 200 && res.data) {
          this.orderInfo = res.data
          // Â¶ÇÊûúÊé•Âè£ËøîÂõû‰∫ÜÈáëÈ¢ùÔºå‰ΩøÁî®Êé•Âè£ËøîÂõûÁöÑÈáëÈ¢ù
          if (res.data.totalAmount) {
            this.totalAmount = Number(res.data.totalAmount).toFixed(2)
          }
        }
      } catch (error) {
        console.error('‚ùå Âä†ËΩΩËÆ¢ÂçïËØ¶ÊÉÖÂ§±Ë¥•:', error)
      }
    },

    /**
     * Âä†ËΩΩ‰ΩôÈ¢ù
     */
    async loadBalance() {
      try {
        const res = await getWalletBalance()
        if (res.code === 200) {
          this.balance = Number(res.data || 0).toFixed(2)
        }
      } catch (error) {
        console.error('‚ùå Âä†ËΩΩ‰ΩôÈ¢ùÂ§±Ë¥•:', error)
      }
    },

    /**
     * ÈÄâÊã©ÊîØ‰ªòÊñπÂºè
     */
    selectPayType(type) {
      this.payType = type
    },

    /**
     * Ëé∑ÂèñÊúçÂä°Á±ªÂûãÂêçÁß∞
     */
    getServiceTypeName(type) {
      const types = {
        1: 'Â∏ÆÊàë‰π∞',
        2: 'Â∏ÆÊàëÈÄÅ',
        3: 'Â∏ÆÊàëÂèñ',
        4: 'ÂÖ®ËÉΩ'
      }
      return types[type] || '-'
    },

    /**
     * Á°ÆËÆ§ÊîØ‰ªò
     */
    async confirmPay() {
      // ‰ΩôÈ¢ù‰∏çË∂≥Ê£ÄÊü•
      if (this.payType === 1 && Number(this.balance) < Number(this.totalAmount)) {
        uni.showModal({
          title: '‰ΩôÈ¢ù‰∏çË∂≥',
          content: `ÂΩìÂâç‰ΩôÈ¢ùÔºö¬•${this.balance}\nÊîØ‰ªòÈáëÈ¢ùÔºö¬•${this.totalAmount}\n\nËØ∑ÂÖàÂÖÖÂÄº`,
          confirmText: 'ÂéªÂÖÖÂÄº',
          cancelText: 'ÂèñÊ∂à',
          success: (res) => {
            if (res.confirm) {
              uni.navigateTo({
                url: '/pages/wallet/recharge'
              })
            }
          }
        })
        return
      }

      // Á°ÆËÆ§ÊîØ‰ªò
      uni.showModal({
        title: 'Á°ÆËÆ§ÊîØ‰ªò',
        content: `Á°ÆÂÆöÊîØ‰ªò ¬•${this.totalAmount} ÂêóÔºü`,
        success: async (res) => {
          if (res.confirm) {
            await this.doPay()
          }
        }
      })
    },

    /**
     * ÊâßË°åÊîØ‰ªò
     */
    async doPay() {
      if (this.paying) return

      try {
        this.paying = true
        uni.showLoading({ title: 'ÊîØ‰ªò‰∏≠...' })

        const res = await payOrder({
          orderId: this.orderId,
          payType: this.payType
          // payPassword: '123456' // Â¶ÇÊûúÈúÄË¶ÅÊîØ‰ªòÂØÜÁ†Å
        })

        uni.hideLoading()
        this.paying = false

        if (res.code === 200) {
          uni.showToast({
            title: 'ÊîØ‰ªòÊàêÂäü',
            icon: 'success',
            duration: 1500
          })

          // Ë∑≥ËΩ¨Âà∞ËÆ¢ÂçïËØ¶ÊÉÖÈ°µ
          setTimeout(() => {
            uni.redirectTo({
              url: `/pages/order/detail?id=${this.orderId}`
            })
          }, 1500)
        } else {
          uni.showToast({
            title: res.message || 'ÊîØ‰ªòÂ§±Ë¥•',
            icon: 'none'
          })
        }
      } catch (error) {
        uni.hideLoading()
        this.paying = false
        console.error('‚ùå ÊîØ‰ªòÂ§±Ë¥•:', error)
        uni.showToast({
          title: 'ÊîØ‰ªòÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï',
          icon: 'none'
        })
      }
    },

    /**
     * ËøîÂõû‰∏ä‰∏ÄÈ°µ
     */
    goBack() {
      uni.navigateBack()
    }
  }
}
</script>

<style lang="scss" scoped>
.payment-container {
  min-height: 100vh;
  background-color: #f5f5f5;
  display: flex;
  flex-direction: column;
}

/* ÂØºËà™Ê†è */
.nav-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 44px;
  padding: 0 15px;
  background-color: #fff;
  border-bottom: 1px solid #eee;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
}

.nav-back {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  color: #333;
  cursor: pointer;
}

.nav-title {
  font-size: 17px;
  font-weight: 600;
  color: #333;
}

.nav-placeholder {
  width: 40px;
}

/* ÊîØ‰ªòÂÜÖÂÆπ */
.payment-content {
  flex: 1;
  margin-top: 54px;
  margin-bottom: 70px;
  padding: 15px;
  overflow-y: auto;
}

/* ÈáëÈ¢ùÂç°Áâá */
.amount-card {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 16px;
  padding: 30px 20px;
  text-align: center;
