  justify-content: center;
}

.amount-symbol {
  font-size: 32px;
  color: #fff;
  margin-right: 4px;
}

.amount-number {
  font-size: 56px;
  font-weight: bold;
  color: #fff;
  line-height: 1;
}

/* ËÆ¢Âçï‰ø°ÊÅØ */
.order-info-section,
.payment-method-section,
.tips-section {
  background-color: #fff;
